"use strict";
import { getCreate2Address } from "@ethersproject/address";
import { keccak256, pack } from "@ethersproject/solidity";
import { Token, V2_FACTORY_ADDRESSES } from "@uniswap/sdk-core";
import { LightCard } from "components/Card/cards";
import MigrateSushiPositionCard from "components/PositionCard/Sushi";
import MigrateV2PositionCard from "components/PositionCard/V2";
import { SwitchLocaleLink } from "components/SwitchLocaleLink";
import { V2Unsupported } from "components/V2Unsupported";
import { AutoColumn } from "components/deprecated/Column";
import { Dots } from "components/swap/styled";
import { useAccount } from "hooks/useAccount";
import { useNetworkSupportsV2 } from "hooks/useNetworkSupportsV2";
import { PairState, useV2Pairs } from "hooks/useV2Pairs";
import { useRpcTokenBalancesWithLoadingIndicator } from "lib/hooks/useCurrencyBalance";
import styled, { useTheme } from "lib/styled-components";
import { BodyWrapper } from "pages/App/AppBody";
import { useCallback, useMemo } from "react";
import { Trans } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import { Text } from "rebass";
import { toV2LiquidityToken, useTrackedTokenPairs } from "state/user/hooks";
import { ThemedText } from "theme/components";
import { StyledInternalLink } from "theme/components/Links";
import { Flex, TouchableArea } from "ui/src";
import { Arrow } from "ui/src/components/arrow/Arrow";
import { iconSizes } from "ui/src/theme";
import Trace from "uniswap/src/features/telemetry/Trace";
import { InterfacePageNameLocal } from "uniswap/src/features/telemetry/constants";
export const MigrateHeader = styled(ThemedText.H1Small)`
  font-weight: 535;
`;
function EmptyState({ message }) {
  return <AutoColumn style={{ minHeight: 200, justifyContent: "center", alignItems: "center" }}><ThemedText.DeprecatedBody>{message}</ThemedText.DeprecatedBody></AutoColumn>;
}
const computeSushiPairAddress = ({ tokenA, tokenB }) => {
  const [token0, token1] = tokenA.sortsBefore(tokenB) ? [tokenA, tokenB] : [tokenB, tokenA];
  return getCreate2Address(
    "0xC0AEe478e3658e2610c5F7A4A2E1777cE9e4f2Ac",
    keccak256(["bytes"], [pack(["address", "address"], [token0.address, token1.address])]),
    "0xe18a34eb0e04b04f7a0ac29a6e80748dca96319b42c54d679cb821dca90c6303"
  );
};
function toSushiLiquidityToken([tokenA, tokenB]) {
  return new Token(tokenA.chainId, computeSushiPairAddress({ tokenA, tokenB }), 18, "SLP", "SushiSwap LP Token");
}
export default function MigrateV2() {
  const theme = useTheme();
  const account = useAccount();
  const navigate = useNavigate();
  const { state } = useLocation();
  const v2FactoryAddress = account.chainId ? V2_FACTORY_ADDRESSES[account.chainId] : void 0;
  const trackedTokenPairs = useTrackedTokenPairs();
  const tokenPairsWithLiquidityTokens = useMemo(
    () => trackedTokenPairs.map((tokens) => {
      const sushiLiquidityToken = account.chainId === 1 ? toSushiLiquidityToken(tokens) : null;
      return {
        v2liquidityToken: v2FactoryAddress ? toV2LiquidityToken(tokens) : void 0,
        sushiLiquidityToken,
        tokens
      };
    }),
    [trackedTokenPairs, account.chainId, v2FactoryAddress]
  );
  const allLiquidityTokens = useMemo(() => {
    const v2 = tokenPairsWithLiquidityTokens.map(({ v2liquidityToken }) => v2liquidityToken);
    const sushi = tokenPairsWithLiquidityTokens.map(({ sushiLiquidityToken }) => sushiLiquidityToken).filter((token) => !!token);
    return [...v2, ...sushi];
  }, [tokenPairsWithLiquidityTokens]);
  const [pairBalances, fetchingPairBalances] = useRpcTokenBalancesWithLoadingIndicator(
    account.address,
    allLiquidityTokens
  );
  const tokenPairsWithV2Balance = useMemo(() => {
    if (fetchingPairBalances) {
      return [];
    }
    return tokenPairsWithLiquidityTokens.filter(({ v2liquidityToken }) => v2liquidityToken && pairBalances[v2liquidityToken.address]?.greaterThan(0)).map((tokenPairsWithLiquidityTokens2) => tokenPairsWithLiquidityTokens2.tokens);
  }, [fetchingPairBalances, tokenPairsWithLiquidityTokens, pairBalances]);
  const tokenPairsWithSushiBalance = useMemo(() => {
    if (fetchingPairBalances) {
      return [];
    }
    return tokenPairsWithLiquidityTokens.filter(
      ({ sushiLiquidityToken }) => !!sushiLiquidityToken && pairBalances[sushiLiquidityToken.address]?.greaterThan(0)
    );
  }, [fetchingPairBalances, tokenPairsWithLiquidityTokens, pairBalances]);
  const v2Pairs = useV2Pairs(tokenPairsWithV2Balance);
  const v2IsLoading = fetchingPairBalances || v2Pairs.some(([pairState]) => pairState === PairState.LOADING);
  const handleNavigateBack = useCallback(() => {
    if (state?.from) {
      navigate(state.from);
    } else {
      navigate("/positions");
    }
  }, [navigate, state?.from]);
  const networkSupportsV2 = useNetworkSupportsV2();
  if (!networkSupportsV2) {
    return <V2Unsupported />;
  }
  return <Trace logImpression page={InterfacePageNameLocal.MigrateV2}><BodyWrapper style={{ padding: 24 }}><Flex gap="$gap16"><Flex row alignItems="center" justifyContent="space-between" gap="$gap8"><TouchableArea
    p="$spacing6"
    borderRadius="$rounded8"
    onPress={handleNavigateBack}
    hoverable
    hoverStyle={{
      backgroundColor: "$backgroundHover"
    }}
  ><Arrow direction="w" color="$neutral1" size={iconSizes.icon24} /></TouchableArea><MigrateHeader><Trans i18nKey="migrate.v2Title" /></MigrateHeader><Flex width={iconSizes.icon48} height={iconSizes.icon36} /></Flex><Text m="$spacing8" lineHeight="24px" variant="body2"><Trans i18nKey="migrate.v2Instruction" /></Text>{!account ? <LightCard padding="40px"><ThemedText.DeprecatedBody color={theme.neutral3} textAlign="center"><Trans i18nKey="migrate.connectWallet" /></ThemedText.DeprecatedBody></LightCard> : v2IsLoading ? <LightCard padding="40px"><ThemedText.DeprecatedBody color={theme.neutral3} textAlign="center"><Dots><Trans i18nKey="common.loading" /></Dots></ThemedText.DeprecatedBody></LightCard> : v2Pairs.filter(([, pair]) => !!pair).length > 0 ? <>{v2Pairs.filter(([, pair]) => !!pair).map(([, pair]) => <MigrateV2PositionCard key={pair.liquidityToken.address} pair={pair} />)}{tokenPairsWithSushiBalance.map(({ sushiLiquidityToken, tokens }) => {
    return <MigrateSushiPositionCard
      key={sushiLiquidityToken.address}
      tokenA={tokens[0]}
      tokenB={tokens[1]}
      liquidityToken={sushiLiquidityToken}
    />;
  })}</> : <EmptyState message={<Trans i18nKey="migrate.noV2Liquidity" />} />}<AutoColumn justify="center" gap="md"><Text textAlign="center" fontSize={14} style={{ padding: ".5rem 0 .5rem 0" }}><Trans
    i18nKey="migrate.missingV2Position"
    components={{
      Link: <StyledInternalLink id="import-pool-link" to="/pools/v2/find" />
    }}
  /></Text></AutoColumn></Flex></BodyWrapper><SwitchLocaleLink /></Trace>;
}
